import { defineCustomElement as defineCustomElementIcon } from '@ionic/core/components/ion-icon';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TpPlusMinus } from '../plusminus';
import { TP_PLUSMINUS } from '../utils/plusminus.constant';
import { TpPlusMinusType } from '../utils/plusminus.type';

defineCustomElementIcon();

const meta: Meta<TpPlusMinus & TpPlusMinusType> = {
  title: 'components/Core/Plusminus',
  argTypes: {
    disabled: {
      control: { type: 'select' },
      options: [...TP_PLUSMINUS.disabled],
      description: 'Define o que estar√° disabled no componente.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: TP_PLUSMINUS.disabled.join('|') },
      },
    },
    size: {
      control: { type: 'check' },
      options: ['xl'],
      description: 'Define o tamanho do componente.',
      table: {
        defaultValue: { summary: 'undefined' },
        type: { summary: ['xl'].join('|') },
      },
    },
  },
};

export default meta;

type Story = StoryObj<TpPlusMinus & TpPlusMinusType>;

export const Default: Story = {
  render: ({ ...args }) => {
    return html`
      <tp-plusminus
        color=${args.color}
        disabled=${args.disabled}
        size=${args.size}
      >
        500
      </tp-plusminus>
    `;
  },
};
